.books {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding: 7vh 15vw;
    box-sizing: border-box;
    min-height: 580px;
}

.book {
    background-color: rgba(51, 51, 51, 0.9);
    margin: 7vh 30vw;
    border: 0.5px solid rgb(150, 150, 150); /* Thinner border */
    color: white;
    border-radius: 10px;
    display: flex;
    flex-direction: row;
    width: 80%; /* Adjust as needed */
    /* box-sizing: border-box; */
}

.book-image {
    width: 150px; /* Fixed width for all images */
    height: auto;
    border-right: 0.5px solid rgb(150, 150, 150); /* Thinner border on the right */
    border-radius: 10px;
    overflow: hidden;
    flex-shrink: 0;
    box-shadow: 4px 0 8px rgba(157, 140, 140, 0.3);

}

.book-image img {
    width: 100%;
    height: auto;
    object-fit: cover;
    border-radius: 10px;
}

.book-content {
    width: calc(100% - 150px); /* Remaining space after the image */
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: left;
    text-align: left;
}

.book-content h6 {
    margin: 0;
    padding:1vw 2vw;
    font-weight:900;
    padding-top:2vw;
}

.book-content p {
    margin: 10px 0 0;
    padding:1vw 2vw;
}


.search-form {
    display: flex;
    align-items: center; /* Center items vertically */
    margin-bottom: 7vh; 
}

.search-input {
    height: 8vh; /* Increase height */
    width: 30vw; /* Increase width */
    border-radius: 20px; /* Rounded corners */
    border: 1px solid #ccc; /* Light border */
    padding: 10px; /* Padding for inner spacing */
    background-color: rgb(51,51,51);; /* Same background color as default */
    margin-right: 10px; /* Space between input and button */
    color: white!important;
}

.search-button {
    height: 40px; /* Same height as input */
    border-radius: 20px; /* Rounded corners */
    border: none; /* Remove default border */
    background-color: rgb(20, 145, 155); /* Set button color to blue */
    color: white; /* Text color */
    padding: 0 20px; /* Horizontal padding */
    cursor: pointer; /* Pointer cursor on hover */
    transition: background-color 0.3s; /* Smooth transition */
}

.search-button:hover {
    background-color: rgb(16, 117, 124);; /* Darker shade on hover */
}

@media (max-width: 768px) {
    .book {
        flex-direction: column; /* Stack items on smaller screens */
    }

    .book-image {
        width: 100%; /* Full width on small screens */
        border-right: none; /* Remove border */
        border-bottom: 0.5px solid rgb(150, 150, 150); /* Add bottom border */
    }

    .book-content {
        width: 100%; /* Full width */
    }
}

