.full-display {
    display: flex;
    height: 100vh;
    min-height: 580px;
    background-color: rgb(51, 51, 51);
    position: relative;
}

/* Left side div with black background */
.black_left {
    flex: 1;
    background-color: rgb(51, 51, 51);
    display: flex;
    justify-content: center;
    align-items: center;
    color: #CECCC8;
    /* padding: 15px; */
    z-index: 2;
}

/* Right side div with image */
.image-container {
    width: 50vw;
    display: flex;
    min-height: 580px;
    align-items: center;
    justify-content: flex-end;
    background-color: rgb(51, 51, 51);
}
.image-container img {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
    min-height: 580px;
}

.left-content {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: flex-start;
    text-align: left;
}
.heading-the {
    font-size: 1.5rem;
    padding-left: 15px;
}
.title {
    padding-bottom: 5vh;
    font-size: 5rem;
    padding-left: 10px;
}
.description {
    font-size: 1.5rem;
    padding-left: 20px;
}
.buttons {
    display: block;
    margin-top: 5vh;
    padding-left: 20px;
}
.signup-but {
    background-color: rgb(20, 145, 155);
    padding: 10px 20px;
    margin-right: 2vw;
    /* color: rgb(51, 51, 51); */
    color:white;
    border: none;
    cursor: pointer;
    font-size: 1rem;
    font-weight: 600;
    border-radius: 5px;
    box-shadow: 2px;
}
.signin-but {
    background-color: rgb(51, 51, 51);
    color: white;
    border: none;
    cursor: pointer;
    padding: 10px 20px;
    font-size: 1rem;
    font-weight: 600;
    border-radius: 5px;
    box-shadow: 2px;
    text-decoration: underline;
}

.signin-but a {
    color: white; /* Set the desired link color */
}

.signin-but a:hover {
    color: rgb(20, 145, 155); /* Set the desired link color */
}

.signup-but a{
    color: white;
    text-decoration: none;
}

