    background-color: rgb(16, 117, 124); 
} */

/* Container Styles */
.custom-container {
    max-width: 48rem; /* max-w-4xl */
    margin-left: auto; /* mx-auto */
    margin-right: auto; /* mx-auto */
    padding-left: 1.5rem; /* px-6 */
    padding-right: 1.5rem; /* px-6 */
    padding-top: 1.75rem; /* py-7 */
    padding-bottom: 1.75rem; /* py-7 */
  }

  .custom-heading {
    font-size: 1.875rem; /* text-3xl */
    line-height: 2.25rem; /* matching line-height for text-3xl */
    font-weight: 800; /* font-extrabold */
    color: rgb(51, 51, 51); /* text-white */
    letter-spacing: -0.025em; /* tracking-tight */
  }

  .custom-paragraph {
    margin-top: 1rem; /* mt-4 */
    max-width: 768px; /* max-w-3xl */
    font-size: 1.125rem; /* text-lg */
    line-height: 1.75rem; /* matching line-height for text-lg */
    color: rgb(51,51,51); /* text-cyan-100 */
  }

  .mt-4 {
    margin-top: 1rem; /* 16px */
  }
  
  .max-w-3xl {
    max-width: 48rem; /* 768px */
  }
  
  .text-lg {
    font-size: 1.125rem; /* 18px */
  }
  
  .text-cyan-100 {
    color: rgb(51, 51, 51);
  }
  .text-white {
    color: rgb(51, 51, 51);
  }
  .hidden {
    display: none;
  }
  
  @media (min-width: 640px) {
    .sm\:block {
      display: block;
    }
  }
  .mt-7 {
    margin-top: 1.75rem; /* 28px */
  }
  
  .grid {
    display: grid;
  }
  
  .grid-cols-1 {
    grid-template-columns: repeat(1, 1fr);
  }
  
  .grid-cols-2 {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .grid-cols-4 {
    grid-template-columns: repeat(4, 1fr);
  }
  
  .gap-x-5 {
    column-gap: 1.25rem; /* 20px */
  }
  
  .gap-y-7 {
    row-gap: 1.75rem; /* 28px */
  }
  
  .gap-x-6 {
    column-gap: 1.5rem; /* 24px */
  }
  
  .gap-y-8 {
    row-gap: 2rem; /* 32px */
  }
  
  @media (min-width: 1024px) {
    .lg\:mt-8 {
      margin-top: 2rem; /* 32px */
    }
  
    .lg\:grid-cols-4 {
      grid-template-columns: repeat(4, 1fr);
    }
  
    .lg\:gap-x-6 {
      column-gap: 1.5rem; /* 24px */
    }
  
    .lg\:gap-y-8 {
      row-gap: 2rem; /* 32px */
    }
  }

  .flex {
    display: flex;
  }
  
  .items-center {
    align-items: center;
  }
  
  .justify-center {
    justify-content: center;
  }
  
  .h-7 {
    height: 1.75rem; /* 28px */
  }
  
  .w-7 {
    width: 1.75rem; /* 28px */
  }
  
  .rounded-md {
    border-radius: 0.375rem; /* 6px */
  }
  
  .bg-white\/10 {
    background-color: rgba(255, 255, 255, 0.1);
  }

  .text-white {
    color: rgb(51, 51, 51)!important;
    /* background-color: rgb(51, 51, 51); */
  }
  .h-5{
    height:5;
  }
  .w-5{
    width:5;
  }
  .text-xl {
    font-size: 1.25rem; /* 20px */
  }
  
  .font-medium {
    font-weight: 500;
  }

  /* svg{
    justify-content: center;
    display:flex; 
    align-items: center;
  } */
  
  @media (min-width: 640px) {
    .sm\:text-lg {
      font-size: 1.125rem; /* 18px */
    }
  }
  .text-xl {
    font-size: 1.25rem; /* 20px */
  }
  
  .font-medium {
    font-weight: 500;
  }
  
  @media (min-width: 640px) {
    .sm\:text-lg {
      font-size: 1.125rem; /* 18px */
    }
  }
  .mt-5 {
    margin-top: 1.25rem; /* 20px */
  }
  .text-base {
    font-size: 1rem; /* 16px */
  }
          
  
  
  @media (min-width: 640px) { /* sm:py-8, sm:pt-8 */
    .custom-container {
      padding-top: 2rem; /* sm:pt-8 */
      padding-bottom: 2rem; /* sm:py-8 */
    }
  }
  
  @media (min-width: 1024px) { /* lg:max-w-6xl */
    .custom-container {
      max-width: 72rem; /* lg:max-w-6xl */
    }
  }

/* Media queries for responsiveness */
@media (max-width: 768px) {
    .full-display {
        flex-direction: column;
    }
    .image-container {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
    }
    .image-container img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        opacity: 0.5; /* Adjust as needed for better readability of text */
    }
    .black_left {
        background-color: transparent;
        z-index: 2;
        justify-content: center;
        align-items: center;
        padding: 20px;
    }
    .left-content {
        text-align: center;
        align-items: center;
    }
    .title {
        font-size: 3rem;
    }
    .description {
        font-size: 1rem;
    }
    .buttons {
        margin-top: 2vh;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .signup-but, .signin-but {
        font-size: 0.8rem;
        padding: 8px 16px;
        margin: 10px 0;
    }
}

@media (max-width: 480px) {
    .title {
        font-size: 2rem;
    }
    .description {
        font-size: 0.8rem;
    }
    .signup-but, .signin-but {
        font-size: 0.6rem;
        padding: 6px 12px;
    }
}

