.signup-screen {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    position: relative;
    overflow: hidden;
    min-height:580px;
}

/* Background image container */
.signup-screen-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    overflow: hidden;
    min-height:580px;
}

.signup-screen-img {
    width: 100%;
    height: 100%;
    object-fit: cover; /* Ensures the image covers the entire container */
    opacity: 1; /* Optional: Adjust opacity for better visibility of the form */
    filter: brightness(75%);
}

/* Form container */
.form-container {
    position: relative;
    z-index: 2; /* Ensures the form is above the background image */
    background-color: black; /* Semi-transparent background */
    color:white !important;
    padding: 40px;
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    max-width: 400px;
    text-align: center;
    opacity:0.75;
}

.register-form input{
    background-color: rgba(51,51,51);
    color:white;
    border: none;
    border-bottom: 1px solid black;
}

input:hover{
    color:rgba(51,51,51);
    outline: none !important;
    box-shadow: none !important;
}

input:focus{
    color:rgba(51,51,51);
    /* background-color: transparent !important; */
    outline: none !important;
    box-shadow: none !important;
}

label{
    color: white !important;
}


.register-form a {
    color: rgb(20, 145, 155);; /* Set the desired link color */
    text-decoration: none; /* Remove underline if needed */
}

.register-form a:hover {
    color: white; /* Set the link color on hover */
    text-decoration: underline; /* Add underline on hover if needed */
}

.submit-button{
    margin-top:2vh;
    margin-bottom: 4vh;
    width: 80%; /* Match input width */
    padding:5px;
    background-color: rgb(20, 145, 155); /* Example button background color */
    color: white; /* Button text color */
    border: none;
    cursor: pointer;
    border-radius: 5px;
}

.submit-button:active {
    background-color: #0c6970; /* New background color on click */
}

@media (max-width: 768px) {
    .form-container {
        width: 90%;
        padding: 15px;
    }
}